	display: block;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: #0e1d41;
	content: '';
	z-index: -1;
	-webkit-transition: all 600ms cubic-bezier(.25,.62,.27,.99);
	transition: all 600ms cubic-bezier(.25,.62,.27,.99);
}
.home_slider_nav:hover::after
