  align-items: center;
  height: 2em;
  width: 1.2em;
  font-size: 22px;
  color: rgba(#fff, .5);
  border-radius: 2em;
  border: solid 2px;
  -webkit-transition: .4s;
  transition: .4s;
  color: #fff;
}
